@import './variables';

@mixin vertical {
  display: flex;
  flex-direction: column;
}

@mixin horizontal {
  display: flex;
  flex-direction: row;
}

@mixin rounded {
  border-radius: 0.4rem;
}

@mixin mobile {
  @media (max-width: $max-mobile-width) {
    @content;
  }
}

@mixin p-xs($placemenet) {
  @if $placemenet == 'all' {
    padding: $space-xs;
  } @else if $placemenet == 'x' {
    padding-left: $space-xs;
    padding-right: $space-xs;
  } @else if $placemenet == 'y' {
    padding-top: $space-xs;
    padding-bottom: $space-xs;
  } @else {
    padding-#{$placemenet}: $space-xs;
  }
}

